}
/* Individual Caption Styles */
/* Caption Style 2 */
.grid figure .box {
  box-sizing: content-box;
  cursor: pointer;
}
.list-view0.grid figure .box,
.list-view1.grid figure .box,
.list-view2.grid figure .box {
  max-width: 100%;
  display: block;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.list-view0.grid figure .box h4.ellipsis,
.list-view1.grid figure .box h4.ellipsis,
.list-view2.grid figure .box h4.ellipsis {
  height: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.list-view0.grid figure .box h4,
.list-view1.grid figure .box h4,
.list-view2.grid figure .box h4 {
  z-index: 1;
}
.list-view0.grid figure .box h4 a,
.list-view1.grid figure .box h4 a,
.list-view2.grid figure .box h4 a {
  z-index: 1;
}
.no-touch .list-view0 figure .box {
  z-index: 1;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}
.list-view0.grid .ui-state-highlight .img-precontainer {
  background: grey!important;
}
.list-view0.grid .ui-state-highlight .img-precontainer .img-container {
  background: repeating-linear-gradient(45deg, transparent, transparent 5px, rgba(255, 255, 255, 0.4) 5px, rgba(255, 255, 255, 0.3) 10px);
  border: none;
  overflow: hidden;
}
.list-view0.grid .ui-state-hover .img-precontainer .img-container {
  background: #666;
}
.list-view1.grid .ui-state-highlight:nth-child(odd) figure,
.list-view2.grid .ui-state-highlight:nth-child(odd) figure {
  background: #dddddd!important;
  border-bottom-color: #444!important;
}
.list-view1.grid .ui-state-highlight:nth-child(even) figure,
.list-view2.grid .ui-state-highlight:nth-child(even) figure {
  background: #cccccc!important;
  border-bottom-color: #aaa!important;
}
.list-view1.grid .ui-state-highlight.ui-state-hover figure,
.list-view2.grid .ui-state-highlight.ui-state-hover figure {
  background-color: #aaa!important;
}
.no-touch .list-view0 figure:hover .box,
.no-touch .list-view0 figure.cs-hover .box {
  box-shadow: 0px 0px 4px 0px rgba(1, 1, 1, 0.5);
  -webkit-transform: translateY(-26px);
  -ms-transform: translateY(-26px);
  transform: translateY(-26px);
}
.list-view0 figure:hover .box.no-effect,
.list-view0 figure.cs-hover .box.no-effect,
.no-effect {
  box-shadow: none;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
}
