  background-color: #fff;
  position: relative;
  height: auto;
  word-wrap: break-word;
  -webkit-user-select: none;
  -moz-user-select: 0;
  -ms-user-select: none;
  user-select: none;
  padding: 5px 5px 5px 30px;
}
